




































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class UiInputPassword extends Vue {
    @Prop() placeholder: string

    @Prop() icon: string

    @Prop() errorText: string

    @Prop() value: string

    @Prop() hits: string

    type: string = 'password'

    hitsActive: boolean = false

    $refs: Vue['$refs'] & {
        passwordInput: HTMLInputElement
    }

    onInput($event: any) {
        this.$emit('input', $event.target.value)
    }

    toggleTypeInput() {
        if (this.type === 'text') {
            this.type = 'password'
        } else {
            this.type = 'text'
        }
        this.$refs.passwordInput.focus()
    }

    focusInput() {
        this.hitsActive = true
    }

    blurInput() {
        if (this.$refs.passwordInput.value.length) {
            this.hitsActive = true
        } else {
            this.hitsActive = false
        }
    }
}
