

































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class UiInput extends Vue {
    @Prop() placeholder: string
    @Prop() icon: boolean
    @Prop() errorText: string
    @Prop() label: string
    @Prop() disabled: boolean
    @Prop() hits: string
    @Prop() value: string

    isHitsActive: boolean = false

    $refs: Vue['$refs'] & {
        input: HTMLInputElement
    }

    mounted() {
        if (this.value) {
            this.isHitsActive = true
        }
    }

    onInput($event: any) {
        this.$emit('input', $event.target.value)
    }

    focusInput() {
        this.isHitsActive = true
    }

    blurInput() {
        if (this.$refs.input.value.length) {
            this.isHitsActive = true
        } else {
            this.isHitsActive = false
        }
    }
}
